<template>
  <div>
    <div>food:{{ food }}</div>
    <div>feel:{{ feel }}</div>
    <div>sleep:{{ sleep }}</div>
    <div>state:{{ IfState }}</div>
    <div>{{ showIt }}</div>
    <button @click="feedIt">餵食</button>
    <button @click="slapIt">摸牠</button>
    <button @click="sleepIt">睡覺</button>
    <button @click="deposition">不理牠</button>
    <div><img :src="Ifimage"></div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      showIt:'',
      food:50,
      feel:50,
      sleep:50,
      state:'',
    }
  },
  computed:{
    IfState(){
      if(this.food>=100 && this.feel>=50 && this.sleep>0){
        return "快樂的大肥貓";
      }else if(this.food>=100 && this.feel>=20 && this.sleep>0){
        return "大肥貓"
      }else if(this.food<30 && this.feel>=50 && this.sleep>0){
        return "骷髏弓箭貓"
      }else if(this.food<0 && this.feel>0 && this.sleep>0){
        return "死靈貓"
      }else if(this.feel<0 && this.food>0 && this.sleep>0){
        return "殺手貓"
      }else if(this.feel>0 && this.food>0 &&this.sleep<0){
        return "熬夜貓"
      }else if(this.food<0 && this.feel<0 && this.sleep<0){
        return "這還算是貓?"
      }
      return "貓"
    },
    Ifimage(){
      if(this.food>=100 && this.feel>=50 && this.sleep>0){
        return require('@/assets/快樂的大肥貓.jpg');
      }else if(this.food>=100 && this.feel>=20 && this.sleep>0){
        return require('@/assets/大肥貓.jpg');
      }else if(this.food<30 && this.feel>=50 && this.sleep>0){
        return require('@/assets/骷髏弓箭貓.jpg');
      }else if(this.food<0 && this.feel>0 && this.sleep>0){
        return require('@/assets/死靈貓.jpg');
      }else if(this.feel<0 && this.food>0 && this.sleep>0){
        return require('@/assets/殺手貓.jpg');
      }else if(this.feel>0 && this.food>0 &&this.sleep<0){
        return require('@/assets/熬夜貓.jpg');
      }else if(this.food<0 && this.feel<0 && this.sleep<0){
        return require('@/assets/這還算是貓.jpg');
      }
      return require('@/assets/就是貓.jpg');
    }
  },
  methods:{
    feedIt(){
      this.food += 15;
      //限制
      if(this.food > 150) {
        this.food = 150;
      }
      //條件
      if(this.food<100){
        this.showIt="牠吃得很開心";
        this.feel += 1;
      }else if(this.food>100){
        this.showIt="牠吃得像隻豬";
        this.feel += 20;
      }
      //sleep
      if(this.sleep <= -50){
        this.sleep = -50
      }else{
        this.sleep -=5;
      }
    },
    slapIt(){    
      //條件
      if(this.feel>=100){
        this.showIt="牠不理你";
      }else if(this.feel<100){
        this.feel +=5;
        this.showIt="牠被摸了";
      }else if(this.feel<=0){
        this.feel +=1;
        this.showIt="牠愛理不理";
      }
      //sleep
      if(this.sleep <= -50){
        this.sleep = -50
      }else{
        this.sleep -=10;
      }
      //food      
      this.food -=5;
    },
    sleepIt(){      
      //條件
      if(this.sleep>=80){
        this.sleep -=5;
        this.food -=20;
        this.showIt="牠在你床上跳上跳下";
      }else if(this.sleep<20){
        this.sleep += 5;
        this.food -= 20;
        this.feel -= 5;
        this.showIt="晚上才睡對貓來說是熬夜吧";
      } else {
        this.sleep += 10;
        this.food -= 10;
        this.showIt = "牠睡著了";
      }
      this.feel -=5;
      //限制
      if(this.sleep > 80){
        this.sleep = 80;
      }
    },
    deposition(){
      this.food -=10;
      this.feel -=10;
      this.sleep -= 10;
      const randomNumber = this.getRandomNumber();
      if(randomNumber>15){
        this.showIt = "牠跑出門了";
      }else{
        this.showIt = "牠在家亂跳";
      }
    },
    getRandomNumber(){
      return Math.floor(Math.random()*101);
    }
  }

}
</script>

